export default {
	data() {
		return {
			indexs:0,
			walletInfo: {}, //用户钱包信息
			fenrunPage: 0,
			fenrunList: [],
			tixianPage: 0,
			tixianList: [],
			bindCard: false, //是否绑定银行卡  默认 无绑定 
			money: 0, //可提现金额
			mix_put_forward: 0, //最低提现金额限制
		}
	},
	onLoad() {
		
	},
	onShow() {
		this.getWalletInfo();
		this.getFenrun();
		this.getTixian();
	},
	onReachBottom() {
		if(this.indexs == 0) {
			this.getFenrun();
		} else if(this.indexs == 1) {
			this.getTixian();
		}
	},
	methods: {
		change(index){
			this.indexs=index;
		},
		getWalletInfo() {
			this.api({
				method: "post",
				url: "User/index"
			}).then(res=> {
				console.log(res);
				this.walletInfo = res.data.date;
				this.money = res.data.date.money;
				this.mix_put_forward = res.data.date.mix_put_forward;
				if(this.walletInfo.back_id == null) {
					this.bindCard = false;
				} else {
					this.bindCard = true; //有银行卡
				}
			})
		},
		getFenrun() { //
			this.fenrunPage++;
			this.api({
				method: "post",
				url: "User/fenrun_list",
				data: {
					page: this.fenrunPage
				}
			}).then(res=> {
				console.log(res);
				if(res.data.date.length > 0) {
					res.data.date.forEach(item=> {
						this.fenrunList.push(item);
					})
				}
			})
		},
		getTixian() {
			this.tixianPage ++;
			this.api({
				method: "post",
				url: "User/tixian_list",
				data: {
					page: this.tixianPage
				}
			}).then(res=> {
				console.log(res);
				if(res.data.date.length > 0) {
					res.data.date.forEach(item=> {
						this.tixianList.push(item);
					})
				}
			})
		},
		toPage(url) {
			uni.navigateTo({
				url: url
			})
		},
		tixian() {
			if(this.bindCard) { //有卡
				uni.redirectTo({
					url: './withdrawal?money=' + this.money + "&mix=" + this.mix_put_forward
				})
			} else { //无卡
				uni.showModal({
					title: '提示',
					content: '您还未绑定银行卡,还无法提现哦',
					showCancel: true,
					cancelText: '取消',
					confirmText: '去绑定',
					success: res => {
						if(res.confirm) {
							this.toPage('./bind_card');
						} else if(res.cancel) {
							// 
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}
		}
		
	}
}
